<template>

	<c-message ref="msgPoll"></c-message>

</template>

<script>

	import Message from '../components/c_message.vue';

	export default {
		name: 'cGameState',
		data(){
			return {
				attrs : {
				},
				state : {
					init : false,
					board_ready : false,
					player_ready : false,
					// player_pause : false,
					poll_ready : false,
					submit_ready : false,
					play : false,
				},

			}
		},	
		methods:{
			init : function(){

				this.$root.$on('state_board', this.board_state );
				this.$root.$on('state_player', this.player_state );
				// this.$root.$on('state_player_pause', this.player_pause );
				this.$root.$on('state_poll', this.poll_state );
				this.$root.$on('state_submit', this.submit_state );
				this.$root.$on('state_reset', this.reset );
				this.$root.$on('state_exit', this.forced_redirect );

				this.state.init = true;

				// let self = this;
				// setTimeout( function(){
				// 	self.$root.$emit('init'); // trigger all 
				// },100);

				
			},	

			board_state : function( input ){
				if( input !== this.state.board_ready ){
					this.state.board_ready = input;
					this.check_game();	

					if( input ){
						// trigger board download ..
						this.$root.$emit('init_player');
					}									
				}
			},
			player_state : function( input ){
				if( input !== this.state.player_ready ){
					this.state.player_ready = input;
					this.check_game();					
				}
			},			
			// player_pause : function( input ){
			// 	if( input !== this.state.player_pause ){
			// 		this.state.player_pause = input;
			// 		this.check_game();					
			// 	}
			// },
			poll_state : function( input ){
				if( input !== this.state.poll_ready ){
					this.state.poll_ready = input;
					this.check_game();

					if( input ){
						// trigger board download ..
						this.$root.$emit('init_board');
						this.$root.$emit('init_submit');
					}

				}
			},
			submit_state : function( input ){
				if( input !== this.state.submit_ready ){
					this.state.submit_ready = input;
					this.check_game();
				}
			},	


			check_game : function(){
				if( this.state.player_pause ){
					this.stop();
					return;
				}

				if( this.init && this.state.board_ready && this.state.poll_ready && this.state.player_ready && this.state.submit_ready){
					this.play();
				} else {
					this.stop();
				}
			},


			play : function(){
				console.log('game state: playing.');
				this.state.play = true;
				let self = this;
				setTimeout( function(){
					self.$root.$emit('state_play', true);
				}, 100);
			},
			stop : function(){
				console.log('game state: stopping.');
				this.state.play = false;
				this.$root.$emit('state_play', false);
			},	
			reset : function(){				
				console.log('game state: resetting.');
				// this.$store.dispatch('game/reset');

				// let self = this;
				// setTimeout( function(){
				// 	self.$root.$emit('state_player_submit');
				// }, 100);

			},
			exit : function(){
				
				this.stop();	

				let self = this;
				setTimeout( function(){
					self.$store.dispatch('game/exit');
				}, 100);

				this.$root.$off('state_board');
				this.$root.$off('state_player');
				this.$root.$off('state_poll');
				this.$root.$off('state_submit');		
				this.$root.$off('state_reset');		

				console.log('state exiting.');	
			},

			forced_redirect : function(){
				let self = this;
				setTimeout( function(){
					self.$router.push( '/' );
				});
			},
		},
		mounted() {
			this.init();
		},
		beforeDestroy(){
			this.exit();
		},		
		components: {
			'c-message' : Message,
		},		
}
</script>

