<template>

	<div>
		<c-word
			v-for="(word, index) in board.words" 
			:key=word.key
			v-bind:wordData=word
			v-bind:state=state>
		</c-word>
	</div>

</template>

<script>




	import { submit } from '../mixins/h_submit.js';
	import Word from '../components/c_word.vue';

	export default {
		name: 'cBoard',
		mixins: [ submit ],
		data(){
			return {
				attrs : {
					words : {
						random : true,
						count : 0,
					},
					server : {
						max_timeouts : 5,
						timing : 1000,
					},
					action : Object,
				},
				state : {
					timeouts : 0,
					ready : false,
					play : false,
				},
				board : {
					words : [],
				},
			}
		},	
		computed : {
		},
		methods : {
			init : function(){

				this.$root.$on('state_reset', this.reset );	
				 this.$root.$on('state_play', this.play );

				let board = this.$store.getters['game/get_instance'].data.board;
				if( board === ''){
					return;
				}

				let action = {
					url : ('/api/board/' + board),
					method : 'GET',
					JSON : false };

				this.attrs.action = action;

				let self = this;
				this.onSubmit( self.attrs.action, self, null, null, self.board_success, self.board_error);

			},

			board_success : function( input ){
				this.$store.dispatch('game/set_board', input.data);
				this.board_setup_words();
				this.ready( true );
			},
			board_error : function( input ){
				if( this.state.timeouts < this.attrs.server.max_timeouts ){
					let self = this;
					setTimeout( function(){
						self.ready( false );
						self.state.timeouts +=1
						self.onSubmit( self.attrs.action, self, null, null, self.board_success, self.board_error);
					}, self.attrs.server.timing );
				}

			},


			array_random : function( input ){
				let tempArray = [];
				for( let i = 0; i < input.length; i++){
					tempArray.push( input[i] );
				}
				// randomize
				for( let count = tempArray.length-1; count >= 0; count--){
					let random = Math.floor(Math.random() * (count + 1));
					let temp = tempArray[count]
					tempArray[count] = tempArray[random];
					tempArray[random] = temp;
				}
				// console.log( tempArray );
				return tempArray;		
			},
			array_words : function( input, max){
				let randomize = this.array_random( input );
				// randomize = input;
				// let randomize = input;
				let tempArray = [];
				let tempMax = Math.min( max, input.length );
				for( let i = 0; i < tempMax; i++){
					let tempWord = { 
						word : randomize[i], 
						selected : false,
						key : this.attrs.words.count };
					tempArray.push( tempWord );

					this.attrs.words.count +=1;

				}
				return tempArray;
			},
			board_setup_words : function(){			
				let board = this.$store.getters['game/get_board'];

				let randomWords = this.array_words( board.data.words, board.data.game.display );

				for(let i=0;i<randomWords.length;i++){
					this.$set(this.board.words, i, randomWords[i]);
				}
			},
			reset : function(){	
				console.log('game board: resetting.');
				this.board_setup_words();
			},
			ready : function( input ){
				if( this.state.ready !== input ){
					this.state.ready = input;
					console.log('board: ' + input );
					this.$root.$emit('state_board', input);					
				}
			},
			play : function( input ){
				this.state.play = input;
			},			
			exit : function(){
				this.$root.$off('init_board');
				this.$root.$off('state_reset');
				this.$root.$off('state_play');
			},
			
		},
		mounted() {
			this.$root.$on('init_board', this.init );
		},
		beforeDestroy(){
			this.exit();
		},		
		components: {
			'c-word' : Word,
		},		
}
</script>

