<template>

	<c-panel>
		<div slot="header">
			Create Board
		</div>

		<form 
			class="form" 
			action="/api/board/create" 
			method ="POST" 
			@submit.prevent="onCreate">

			<div class="field">
				<p class="text label">Name:</p>
				<input 
					class="text" 
					required
					type="string" 
					name="name" 
					placeholder="Name" 
					v-model=form.name_string>
			</div>	
			<div class="field">
				<p class="text label">Words:</p>
				<textarea 
					class="text" 
					required
					type="string" 
					name="words" 
					placeholder="seperate with , or space" 
					v-model=form.word_string
					v-on:change=words_update
					style="height: 3.5rem;"></textarea>
			</div>

 			<c-button ref="btnSubmit">
 				Create
 			</c-button>	

			<c-message ref="msgSubmit"></c-message>

		</form>

		<div slot="footer">

			<div class="tags">

				<transition-group name="tag" class="tag1">

					<c-tag
						v-for="(word, index) in words" :key="word">
							{{ word }}
					</c-tag>

				</transition-group>	

			</div>

		</div>

	</c-panel>

</template>

<script>

	import Button from '../components/c_buttonNew.vue';
	import Panel from '../components/c_panel.vue';
	import Message from '../components/c_message.vue';
	import Tag from '../components/c_tag.vue';

	import { submit } from '../mixins/h_submit.js';


	export default {
		name: 'BoardCreate',
		mixins: [ submit ],
		data(){
			return {
				attrs : {
					server : {
						max_timeouts : 5,
						timing : 1500,
					},
					action : Object,
				},	
				state : {
					init : false,
					timeouts : 0,
				},
				form : {
					name_string : '',
					word_string : '',
				},
				words : [],		
				min_word_length : 3,
			}
		},
		methods:{
			init : function( urls ){
				if( !this.state.init ){
					let object = {
						url : ('/api/board/create' ),
						method : 'POST',
						JSON : true,
						body : {
							name : '',
							words : '',
						},
					};
					this.attrs.action = object;
					this.state.init = true;
				}
			},
			onCreate : function( event ){
				if( this.attrs.action.url !== undefined ){

					this.attrs.action.body.name = this.form.name_string;
					this.attrs.action.body.words = this.form.word_string;

					let self = this;	
					this.onSubmit( this.attrs.action, self, self.$refs.btnSubmit, self.$refs.msgSubmit, self.onSuccess, self.onError);

				}
			},
			onSuccess : function( input ){
				let self = this;
				setTimeout( function(){
					self.$router.push( '/board/' +  input.data.url);
				},100);
			},
			onError : function( input ){
				if( this.state.timeouts < this.attrs.server.max_timeouts ){
					let self = this;
					setTimeout( function(){
						self.state.timeouts +=1
						self.onSubmit();
					}, self.attrs.server.timing );
				}
			},

			words_update : function(){
				this.words = [];
				let temp = this.form.word_string.split(/[, \n\t\r]+/);
				for( let count=0;count<temp.length;count++){
					if( temp[count].length >= this.min_word_length){
						this.words.push( temp[count] );
					}
				}
			},
		},
		mounted(){
			this.init();
		},
		components: {
			'c-button' : Button,
			'c-panel' : Panel,
			'c-message' : Message,
			'c-tag' : Tag,
		},		
}
</script>

