<template>

	<transition 
		name="word" 
		mode="out-in">
		
		<button 
			class="word-square word text text-smal1 delay-1"
			v-bind:class="{ 'is-selected' : wordData.selected }"
			v-on:click=toggle>
			
				<div class="word-content">
					<p> {{ wordData.word }} </p>			
				</div>

		</button>

	</transition>		

</template>

<script>

	export default {
		name: 'cWord',
		data(){
			return {
			}
		},	
		props: {
			state : Object,
			wordData : Object,
		},
		methods : {
			init : function(){
				this.$root.$on('state_reset', this.reset );
			},
			reset : function(){
				this.wordData.selected = false;
			},
			toggle : function(){
				if( this.state.play ){
					if( this.wordData.selected ){
						this.wordData.selected = false;

						this.$store.dispatch('game/score_remove');
						this.$store.dispatch('game/word_remove', this.wordData.word );
					} else {
						this.wordData.selected = true;
						this.$store.dispatch('game/score_add');
						this.$store.dispatch('game/word_add', this.wordData.word );
					}
				}
				this.$root.$emit('state_player_submit');
			},
			exit : function(){
			},
		},
		mounted(){
			this.init();
		},
		beforeDestroy(){
			this.exit();
		},
		components: {
		},
}

</script>

