		padding-bottom: 100%;
	}

	.word-content {
		position: absolute;
		width: 100%;
		height: 100%;
		margin: 0;
		padding-top: calc(50% - 0.5rem);
	}

	.word-enter-active {
		animation: word-anim-in-out 0.5s both;
	}
	.word-leave-active {
		animation: word-anim-in-out 0.5s reverse both;
	}
	@keyframes word-anim-in-out {
